.timeline {
  position: relative;
  width: 100%;
  max-width: 1140px;
  margin: 0 auto;
  padding: 15px 0;
}

.timeline::after {
  content: '';
  position: absolute;
  width: 2px;
  background: #006E51;
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -1px;
}

.abc {
  padding: 15px 30px;
  position: relative;
  background: inherit;
  width: 50%;
}

.abc.left {
  left: 0;
}

.abc.right {
  left: 50%;
}

.abc::after {
  content: '';
  position: absolute;
  width: 16px;
  height: 16px;
  top: calc(50% - 8px);
  right: -8px;
  background: #ffffff;
  border: 2px solid #006E51;
  border-radius: 16px;
  z-index: 1;
}

.abc.right::after {
  left: -8px;
}

.abc::before {
  content: '';
  position: absolute;
  width: 50px;
  height: 2px;
  top: calc(50% - 1px);
  right: 8px;
  background: #006E51;
  z-index: 1;
}

.abc.right::before {
  left: 8px;
}

.abc .date {
  position: absolute;
  display: inline-block;
  top: calc(50% - 8px);
  text-align: center;
  font-size: 30px;
  font-weight: bold;
  color: #fcdc1f;
  text-transform: uppercase;
  letter-spacing: 1px;
  z-index: 1;
}

.abc.left .date {
  right: -75px;
}

.abc.right .date {
  left: -93px;
}

.abc .icon {
  position: absolute;
  display: inline-block;
  width: 40px;
  height: 40px;
  padding: 9px 0;
  top: calc(50% - 20px);
  background: #F6D155;
  border: 2px solid #006E51;
  border-radius: 40px;
  text-align: center;
  font-size: 18px;
  color: #006E51;
  z-index: 1;
}

.abc.left .icon {
  right: 56px;
}

.abc.right .icon {
  left: 56px;
}
