@media (max-width: 767.98px) {
  .timeline::after {
    left: 30px;
  }

  .abc {
    width: 100%;
    padding-left: 52px;
    padding-right: 30px;
  }

  .abc.right {
    left: 0%;
  }

  .abc.left::after, 
  .abc.right::after {
    left: 22px;
  }

  .abc.left::before,
  .abc.right::before {
    left: 38px;
    border-color: transparent #006E51 transparent transparent;
    display: none;
  }

  .abc.left .date,
  .abc.right .date {
    right: auto;
    left: -33px;
  }

  .abc.left .icon,
  .abc.right .icon {
    display: none;
  }

  .abc.left .content,
  .abc.right .content {
    padding: 30px 30px 30px 30px;
    /* border-radius: 500px 0 0 500px; */
  }
}

